// src/pages/users-page/users-page.tsx

import React, { useEffect, useState } from "react";
import {
  Container,
  Typography,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Snackbar,
  Alert,
  IconButton,
} from "@mui/material";
import { Delete, Edit } from "@mui/icons-material";

import axiosInstance from "@/api"; 
import CustomTable, { Column } from "@/shared/components/CustomTable/CustomTable";
import { User } from "@/types/User";

// –û–±–Ω–æ–≤–ª—è–µ–º —Ç–∏–ø –¥–ª—è –¥–∞–Ω–Ω—ã—Ö —Å –±—ç–∫–µ–Ω–¥–∞, —á—Ç–æ–±—ã –æ–Ω —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–æ–≤–∞–ª —Ä–µ–∞–ª—å–Ω–æ—Å—Ç–∏
interface BackendUser {
  _id: string;
  firstName: string;
  lastName: string;
  email: string;
  roles: string[];
  registrationDate: string; // –° –±—ç–∫–µ–Ω–¥–∞ –ø—Ä–∏—Ö–æ–¥–∏—Ç —Å—Ç—Ä–æ–∫–∞!
  // –î–æ–±–∞–≤–ª—è–µ–º –æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–µ –ø–æ–ª—è, –µ—Å–ª–∏ –æ–Ω–∏ –º–æ–≥—É—Ç –ø—Ä–∏–π—Ç–∏
  students?: any[]; // –ó–∞–º–µ–Ω–∏—Ç–µ any –Ω–∞ Student, –µ—Å–ª–∏ –µ—Å—Ç—å –∏–º–ø–æ—Ä—Ç
  trainer?: string;
  trainerEmail?: string;
  firebaseUID?: string;
}

export const UsersPage: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [newRole, setNewRole] = useState<string>("");
  const [actionType, setActionType] = useState<"add" | "remove">("add");
  const [open, setOpen] = useState<boolean>(false);
  const [confirmDeleteOpen, setConfirmDeleteOpen] = useState<boolean>(false);
  const [snackbar, setSnackbar] = useState<{ open: boolean; message: string; severity: "success" | "error" }>({
    open: false,
    message: "",
    severity: "success",
  });

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const response = await axiosInstance.get<BackendUser[]>("/users");

        // –ò–°–ü–†–ê–í–õ–ï–ù–ò–ï: –°–æ–∑–¥–∞–µ–º –æ–±—ä–µ–∫—Ç—ã, –∫–æ—Ç–æ—Ä—ã–µ –ü–û–õ–ù–û–°–¢–¨–Æ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—Ç —Ç–∏–ø—É User
        const mappedUsers: User[] = response.data.map((user: any) => ({
          id: user._id,
          firstName: user.firstName,
          lastName: user.lastName,
          email: user.email,
          roles: user.roles,
          
          // üëá –í–æ—Ç –∫–ª—é—á–µ–≤—ã–µ –∏—Å–ø—Ä–∞–≤–ª–µ–Ω–∏—è, –∫–æ—Ç–æ—Ä—ã–µ —Ä–µ—à–∞—é—Ç –æ—à–∏–±–∫—É
          _id: user._id, // 1. –î–æ–±–∞–≤–ª—è–µ–º –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ–µ –ø–æ–ª–µ _id
          registrationDate: new Date(user.registrationDate), // 2. –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º —Å—Ç—Ä–æ–∫—É –≤ –æ–±—ä–µ–∫—Ç Date

          // 3. (–•–æ—Ä–æ—à–∞—è –ø—Ä–∞–∫—Ç–∏–∫–∞) –ü—Ä–æ–∫–∏–¥—ã–≤–∞–µ–º –æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–µ –ø–æ–ª—è
          firebaseUID: user.firebaseUID,
          students: user.students,
          trainer: user.trainer,
          trainerEmail: user.trainerEmail,
        }));
        setUsers(mappedUsers);
      } catch (error) {
        console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —Å–ø–∏—Å–∫–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π:", error);
      }
    };
    fetchUsers();
  }, []);

  // ... –æ—Å—Ç–∞–ª—å–Ω–æ–π –∫–æ–¥ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ –±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π ...
  const handleOpen = (user: User) => {
    setSelectedUser(user);
    setNewRole("");
    setActionType("add");
    setOpen(true);
  };

  const handleClose = () => {
    setSelectedUser(null);
    setOpen(false);
  };

  const handleConfirmDeleteOpen = (user: User) => {
    setSelectedUser(user);
    setConfirmDeleteOpen(true);
  };

  const handleConfirmDeleteClose = () => {
    setConfirmDeleteOpen(false);
    setSelectedUser(null);
  };

  const handleRoleChange = async () => {
    if (!selectedUser) return;
    try {
      await axiosInstance.put("/admin/update-role", {
        userEmail: selectedUser.email,
        role: newRole,
        action: actionType,
      });

      setUsers(users.map(user =>
        user.id === selectedUser.id
          ? {
              ...user,
              roles: actionType === "add" ? [...user.roles, newRole] : user.roles.filter(role => role !== newRole),
            }
          : user
      ));
      setSnackbar({ open: true, message: "–†–æ–ª—å —É—Å–ø–µ—à–Ω–æ –∏–∑–º–µ–Ω–µ–Ω–∞", severity: "success" });
      handleClose();
    } catch (error) {
      console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–æ–ª–∏:", error);
      setSnackbar({ open: true, message: "–û—à–∏–±–∫–∞ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–æ–ª–∏", severity: "error" });
    }
  };

  const handleDeleteUser = async () => {
    if (!selectedUser) return;
    try {
      await axiosInstance.delete(`/admin/delete-user/${selectedUser.id}`);
      setUsers(users.filter(user => user.id !== selectedUser.id));
      setSnackbar({ open: true, message: "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–¥–∞–ª—ë–Ω", severity: "success" });
      handleConfirmDeleteClose();
    } catch (error) {
      console.error("–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è:", error);
      setSnackbar({ open: true, message: "–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è", severity: "error" });
    }
  };

  const columns: Column<User>[] = [
    { field: "firstName", label: "–ò–º—è" },
    { field: "lastName", label: "–§–∞–º–∏–ª–∏—è" },
    { field: "email", label: "Email" },
    { field: "roles", label: "–†–æ–ª–∏", render: (user) => user.roles.join(', ') },
    {
      field: "actions",
      label: "–î–µ–π—Å—Ç–≤–∏—è",
      render: (user: User) => (
        <>
          <IconButton size="small" onClick={() => handleOpen(user)}>
            <Edit />
          </IconButton>
          <IconButton size="small" color="error" onClick={() => handleConfirmDeleteOpen(user)}>
            <Delete />
          </IconButton>
        </>
      ),
    },
  ];

  return (
    <Container>
      <Typography variant="h4" gutterBottom>–°–ø–∏—Å–æ–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π</Typography>
      <CustomTable columns={columns} data={users} />

      {/* –î–∏–∞–ª–æ–≥–æ–≤—ã–µ –æ–∫–Ω–∞ –±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π... */}
      <Dialog open={open} onClose={handleClose} fullWidth maxWidth="xs">
        <DialogTitle>–£–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Ä–æ–ª—è–º–∏</DialogTitle>
        <DialogContent>
          <DialogContentText>
            –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å: <strong>{selectedUser?.firstName} {selectedUser?.lastName}</strong>
          </DialogContentText>
          <FormControl fullWidth sx={{ marginTop: 2 }}>
            <InputLabel>–†–æ–ª—å</InputLabel>
            <Select value={newRole} onChange={(e) => setNewRole(e.target.value)} label="–†–æ–ª—å">
              <MenuItem value="student">–°—Ç—É–¥–µ–Ω—Ç</MenuItem>
              <MenuItem value="coach">–¢—Ä–µ–Ω–µ—Ä</MenuItem>
            </Select>
          </FormControl>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>–û—Ç–º–µ–Ω–∞</Button>
          <Button onClick={handleRoleChange} color="primary" disabled={!newRole}>–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</Button>
        </DialogActions>
      </Dialog>
      <Dialog open={confirmDeleteOpen} onClose={handleConfirmDeleteClose}>
        <DialogTitle>–ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç–µ —É–¥–∞–ª–µ–Ω–∏–µ</DialogTitle>
        <DialogContent>
            <DialogContentText>
                –í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è {selectedUser?.email}? –≠—Ç–æ –¥–µ–π—Å—Ç–≤–∏–µ –Ω–µ–æ–±—Ä–∞—Ç–∏–º–æ.
            </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleConfirmDeleteClose}>–ù–µ—Ç</Button>
          <Button onClick={handleDeleteUser} color="error">–î–∞, —É–¥–∞–ª–∏—Ç—å</Button>
        </DialogActions>
      </Dialog>
      <Snackbar open={snackbar.open} autoHideDuration={4000} onClose={() => setSnackbar({ ...snackbar, open: false })}>
        <Alert severity={snackbar.severity} onClose={() => setSnackbar({ ...snackbar, open: false })} variant="filled">
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Container>
  );
};