// src/shared/components/CustomTable/CustomTable.tsx

import React, { useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  TablePagination,
} from '@mui/material';

// –£–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—ã–π —Ç–∏–ø —Å—Ç—Ä–æ–∫–∏ —Ç–∞–±–ª–∏—Ü—ã. –ì–∞—Ä–∞–Ω—Ç–∏—Ä—É–µ—Ç –Ω–∞–ª–∏—á–∏–µ `id`.
interface RowData {
  id: string | number;
  [key: string]: any;
}

// –ò–°–ü–†–ê–í–õ–ï–ù–ò–ï: –ú—ã —ç–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º —ç—Ç–æ—Ç —Ç–∏–ø, —á—Ç–æ–±—ã –¥—Ä—É–≥–∏–µ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—ã –º–æ–≥–ª–∏ –µ–≥–æ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å.
// –¢–∞–∫–∂–µ, `field` —Ç–µ–ø–µ—Ä—å –º–æ–∂–µ—Ç –±—ã—Ç—å —Å–ø–µ—Ü–∏–∞–ª—å–Ω—ã–º –∑–Ω–∞—á–µ–Ω–∏–µ–º 'actions',
// –∞ –¥–ª—è —Ç–∞–∫–∏—Ö –ø–æ–ª–µ–π –º—ã –¥–æ–±–∞–≤–∏–º –æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω—ã–π `render`.
export interface Column<T> {
  field: keyof T | 'actions';
  label: string;
  render?: (row: T) => React.ReactNode;
}

// –ü—Ä–æ–ø—Å—ã –¥–ª—è –Ω–∞—à–µ–≥–æ —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω–æ–≥–æ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞.
interface CustomTableProps<T extends RowData> {
  columns: Column<T>[];
  data: T[];
}

const CustomTable = <T extends RowData>({ columns, data }: CustomTableProps<T>) => {
  const [search, setSearch] = useState<string>('');
  const [rowsPerPage, setRowsPerPage] = useState<number>(10);
  const [page, setPage] = useState<number>(0);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(event.target.value);
    setPage(0); // –°–±—Ä–∞—Å—ã–≤–∞–µ–º –Ω–∞ –ø–µ—Ä–≤—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É –ø—Ä–∏ –ø–æ–∏—Å–∫–µ
  };

  const handleChangePage = (_event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  
  const filteredData = data.filter((row) =>
    columns.some((column) => {
      // –ù–µ –∏—â–µ–º –ø–æ –∫–æ–ª–æ–Ω–∫–µ 'actions'
      if (column.field === 'actions') return false;
      const value = row[column.field as keyof T];
      return value?.toString().toLowerCase().includes(search.toLowerCase());
    })
  );

  return (
    <Paper>
      <TextField
        fullWidth
        label="–ü–æ–∏—Å–∫"
        variant="outlined"
        value={search}
        onChange={handleSearch}
        style={{ padding: '16px' }}
      />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell key={String(column.field)}>{column.label}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {filteredData.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => (
              <TableRow key={row.id}>
                {columns.map((column) => (
                  <TableCell key={String(column.field)}>
                    {column.render ? column.render(row) : row[column.field as keyof T]}
                  </TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 20, 50, 100]}
        component="div"
        count={filteredData.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
};

export default CustomTable;