import React, { useMemo } from 'react';
import { Stack, Box, Typography, Skeleton, Paper, Divider } from '@mui/material';
import { ScheduleEvent } from '@/types/SheduleEvent';
import { EventCard } from '@/components/EventCard/EventCard'; // –ü–µ—Ä–µ–∏—Å–ø–æ–ª—å–∑—É–µ–º –Ω–∞—à—É —É–Ω–∏–≤–µ—Ä—Å–∞–ª—å–Ω—É—é –∫–∞—Ä—Ç–æ—á–∫—É

interface LessonsListProps {
  schedule: ScheduleEvent[];
  isLoading: boolean;
  onEventSelect: (event: ScheduleEvent) => void;
}

export const LessonsList: React.FC<LessonsListProps> = ({ schedule, isLoading, onEventSelect }) => {
  // --- –õ–û–ì–ò–ö–ê –ó–ê–ì–†–£–ó–ö–ò ---
  if (isLoading) {
    return (
      <Stack spacing={2}>
        <Skeleton variant="rectangular" height={118} sx={{ borderRadius: 2, bgcolor: 'grey.800' }} />
        <Skeleton variant="rectangular" height={118} sx={{ borderRadius: 2, bgcolor: 'grey.800' }} />
        <Skeleton variant="rectangular" height={118} sx={{ borderRadius: 2, bgcolor: 'grey.800' }} />
      </Stack>
    );
  }

  // --- START: –ù–û–í–ê–Ø –õ–û–ì–ò–ö–ê –†–ê–ó–î–ï–õ–ï–ù–ò–Ø –ò –°–û–†–¢–ò–†–û–í–ö–ò ---
  const { upcomingEvents, pastEvents } = useMemo(() => {
    const now = new Date();
    const upcoming: ScheduleEvent[] = [];
    const past: ScheduleEvent[] = [];

    schedule.forEach(event => {
      if (new Date(event.date) >= now) {
        upcoming.push(event);
      } else {
        past.push(event);
      }
    });

    // –°–æ—Ä—Ç–∏—Ä—É–µ–º –ø—Ä–µ–¥—Å—Ç–æ—è—â–∏–µ –æ—Ç –±–ª–∏–∂–∞–π—à–µ–≥–æ –∫ –¥–∞–ª—å–Ω–µ–º—É
    upcoming.sort((a, b) => new Date(a.date).getTime() - new Date(b.date).getTime());
    // –°–æ—Ä—Ç–∏—Ä—É–µ–º –ø—Ä–æ—à–µ–¥—à–∏–µ –æ—Ç —Å–∞–º–æ–≥–æ –Ω–µ–¥–∞–≤–Ω–µ–≥–æ –∫ —Å–∞–º–æ–º—É —Å—Ç–∞—Ä–æ–º—É (–æ–±—Ä–∞—Ç–Ω–∞—è —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞)
    past.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());

    return { upcomingEvents: upcoming, pastEvents: past };
  }, [schedule]);
  // --- END: –ù–û–í–ê–Ø –õ–û–ì–ò–ö–ê ---

  // –ï—Å–ª–∏ –≤–æ–æ–±—â–µ –Ω–µ—Ç —É—Ä–æ–∫–æ–≤
  if (schedule.length === 0) {
    return (
      <Typography sx={{ color: 'grey.500', textAlign: 'center', p: 4 }}>
        You have no scheduled lessons yet.
      </Typography>
    );
  }

  return (
    // –ò—Å–ø–æ–ª—å–∑—É–µ–º React Fragment –¥–ª—è –≥—Ä—É–ø–ø–∏—Ä–æ–≤–∫–∏ –Ω–µ—Å–∫–æ–ª—å–∫–∏—Ö —Å–µ–∫—Ü–∏–π
    <>
      {/* --- –°–ï–ö–¶–ò–Ø –ü–†–ï–î–°–¢–û–Ø–©–ò–• –£–†–û–ö–û–í --- */}
      <Box mb={5}>
        <Typography variant="h5" fontWeight="bold" gutterBottom>
          Upcoming
        </Typography>
        {upcomingEvents.length > 0 ? (
          <Stack spacing={2}>
            {upcomingEvents.map(event => (
              <EventCard key={event._id} event={event} onSelect={onEventSelect} />
            ))}
          </Stack>
        ) : (
          <Paper sx={{ p: 4, textAlign: 'center' }}>
            <Typography color="text.secondary">You have no upcoming lessons.</Typography>
          </Paper>
        )}
      </Box>

      <Divider sx={{ borderColor: 'rgba(255, 255, 255, 0.12)' }} />

      {/* --- –°–ï–ö–¶–ò–Ø –ê–†–•–ò–í–ê (–ü–†–û–®–ï–î–®–ò–• –£–†–û–ö–û–í) --- */}
      <Box mt={4}>
        <Typography variant="h5" fontWeight="bold" gutterBottom>
          Archive
        </Typography>
        {pastEvents.length > 0 ? (
          <Stack spacing={2}>
            {pastEvents.map(event => (
              <EventCard key={event._id} event={event} onSelect={onEventSelect} />
            ))}
          </Stack>
        ) : (
          <Paper sx={{ p: 4, textAlign: 'center' }}>
            <Typography color="text.secondary">Your lesson history is empty.</Typography>
          </Paper>
        )}
      </Box>
    </>
  );
};