// –§–∞–π–ª: components/StudentSchedule/StudentsSchedule.tsx

import React, { useMemo } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import { Paper, Tooltip, Typography } from '@mui/material';

// –ò–∫–æ–Ω–∫–∏
import EventNoteIcon from '@mui/icons-material/EventNote';
import PeopleIcon from '@mui/icons-material/People';
import AssignmentIcon from '@mui/icons-material/Assignment';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';

// –¢–∏–ø—ã
import { ScheduleEvent } from '@/types/SheduleEvent';

// –°—Ç–∏–ª–∏
import 'react-big-calendar/lib/css/react-big-calendar.css';
import './styles.scss';

// --- –ù–ê–°–¢–†–û–ô–ö–ò –ò –£–¢–ò–õ–ò–¢–´ ---
const localizer = momentLocalizer(moment);

const eventTypeIcons = {
  individual_lesson: <EventNoteIcon fontSize="small" />,
  group_lesson: <PeopleIcon fontSize="small" />,
  homework: <AssignmentIcon fontSize="small" />,
  opening_study: <MenuBookIcon fontSize="small" />,
  tournament_participation: <EmojiEventsIcon fontSize="small" />,
};

// --- –ö–û–ú–ü–û–ù–ï–ù–¢–´ –î–õ–Ø –ö–ê–õ–ï–ù–î–ê–†–Ø ---

// –ù–∞—à –∫–∞—Å—Ç–æ–º–Ω—ã–π –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –¥–ª—è —Å–æ–±—ã—Ç–∏—è –≤ –∫–∞–ª–µ–Ω–¥–∞—Ä–µ
const CustomEvent: React.FC<{ event: ScheduleEvent & { start?: Date; end?: Date; } }> = ({ event }) => {
  const icon = eventTypeIcons[event.type as keyof typeof eventTypeIcons];
  const className = `custom-event rbc-event--${event.type}`;
  return (
    <Tooltip title={event.description || event.title} placement="top">
      <div className={className}>
        {icon}
        <Typography variant="caption" noWrap component="span">{event.title}</Typography>
      </div>
    </Tooltip>
  );
};

// --- –û–°–ù–û–í–ù–û–ô –ö–û–ú–ü–û–ù–ï–ù–¢ –ö–ê–õ–ï–ù–î–ê–†–Ø ---

// –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –∫–∞–∫–∏–µ –ø—Ä–æ–ø—Å—ã –±—É–¥–µ—Ç –ø—Ä–∏–Ω–∏–º–∞—Ç—å –Ω–∞—à –∫–æ–º–ø–æ–Ω–µ–Ω—Ç
interface StudentScheduleProps {
  events: ScheduleEvent[]; // –ú–∞—Å—Å–∏–≤ —Å–æ–±—ã—Ç–∏–π –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è
  onSelectEvent: (event: ScheduleEvent) => void; // –§—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –≤—ã–∑–æ–≤–µ—Ç—Å—è –ø—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —Å–æ–±—ã—Ç–∏–µ
}

const StudentSchedule: React.FC<StudentScheduleProps> = ({ events, onSelectEvent }) => {

  // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º –≤—Ö–æ–¥—è—â–∏–µ —Å–æ–±—ã—Ç–∏—è –≤ —Ñ–æ—Ä–º–∞—Ç, –∫–æ—Ç–æ—Ä—ã–π –ø–æ–Ω–∏–º–∞–µ—Ç react-big-calendar
  const calendarEvents = useMemo(() => {
    return events.map(event => ({
      ...event, // –ö–æ–ø–∏—Ä—É–µ–º –≤—Å–µ –ø–æ–ª—è –∏–∑ –Ω–∞—à–µ–≥–æ –æ—Ä–∏–≥–∏–Ω–∞–ª—å–Ω–æ–≥–æ —Å–æ–±—ã—Ç–∏—è
      start: new Date(event.date),
      end: moment(event.date).add(1, 'hour').toDate(), // –î–ª–∏—Ç–µ–ª—å–Ω–æ—Å—Ç—å 1 —á–∞—Å –¥–ª—è –ø—Ä–∏–º–µ—Ä–∞
    }));
  }, [events]); // –≠—Ç–æ—Ç —Ö—É–∫ –±—É–¥–µ—Ç –ø–µ—Ä–µ–∑–∞–ø—É—Å–∫–∞—Ç—å—Å—è —Ç–æ–ª—å–∫–æ –µ—Å–ª–∏ –∏–∑–º–µ–Ω–∏—Ç—Å—è –ø—Ä–æ–ø—Å events

  return (
    <Paper className="schedule-paper" style={{ height: '70vh', padding: '16px' }}>
        <Calendar
          localizer={localizer}
          events={calendarEvents} // <-- –ò—Å–ø–æ–ª—å–∑—É–µ–º –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–Ω—ã–µ —Å–æ–±—ã—Ç–∏—è
          startAccessor="start"
          endAccessor="end"
          views={['month', 'week', 'day', 'agenda']}
          components={{ event: CustomEvent as any }}
          onSelectEvent={onSelectEvent} // <-- –ü—Ä–∏–≤—è–∑—ã–≤–∞–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–ª–∏–∫–∞
          className="schedule-calendar-standalone"
        />
    </Paper>
  );
};

export default StudentSchedule;