import React, { useEffect, useState } from 'react';
import {
  IconButton,
  Badge,
  Menu,
  MenuItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton as MuiIconButton,
  Divider,
  Box,
  Typography
} from '@mui/material';
import NotificationsIcon from '@mui/icons-material/Notifications';
import DeleteIcon from '@mui/icons-material/Delete';
import { getNotifications, markAsRead, deleteNotification } from '@/api/notifications';

interface Notification {
  _id: string;
  type: string;
  content: string;
  read: boolean;
  createdAt: string;
}

interface NotificationsProps {
  navigateToRequests: () => void;
}

export const Notifications: React.FC<NotificationsProps> = ({ navigateToRequests }) => {
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);

  const open = Boolean(anchorEl);
  const unreadCount = notifications.filter(n => !n.read).length;

  const fetchNotifications = async () => {
    try {
      const data = await getNotifications();
      setNotifications(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π:', error);
    }
  };

  useEffect(() => {
    fetchNotifications();
    const interval = setInterval(fetchNotifications, 30000); // –û–±–Ω–æ–≤–ª—è–µ–º –∫–∞–∂–¥—ã–µ 30 —Å–µ–∫—É–Ω–¥
    return () => clearInterval(interval); // –û—á–∏—â–∞–µ–º –∏–Ω—Ç–µ—Ä–≤–∞–ª –ø—Ä–∏ —Ä–∞–∑–º–æ–Ω—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏
  }, []);

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
    // –ü—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ –º–æ–∂–Ω–æ –ø–æ–º–µ—Ç–∏—Ç—å –≤–∏–¥–∏–º—ã–µ –∫–∞–∫ –ø—Ä–æ—á–∏—Ç–∞–Ω–Ω—ã–µ –∏–ª–∏ –ø—Ä–æ—Å—Ç–æ –æ–±–Ω–æ–≤–∏—Ç—å
    fetchNotifications(); 
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  // üëá –ò–°–ü–†–ê–í–õ–ï–ù–ò–ï: –î–æ–±–∞–≤–ª—è–µ–º `event` –≤ –∞—Ä–≥—É–º–µ–Ω—Ç—ã
  const handleDeleteNotification = async (event: React.MouseEvent, notificationId: string) => {
    event.stopPropagation(); // üëà –ì–õ–ê–í–ù–û–ï –ò–°–ü–†–ê–í–õ–ï–ù–ò–ï! –û—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –≤—Å–ø–ª—ã—Ç–∏–µ.
    try {
      await deleteNotification(notificationId);
      // –û–ø—Ç–∏–º–∏—Å—Ç–∏—á–Ω–æ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ UI –¥–ª—è –º–≥–Ω–æ–≤–µ–Ω–Ω–æ–π —Ä–µ–∞–∫—Ü–∏–∏
      setNotifications(prev => prev.filter(n => n._id !== notificationId));
    } catch (error) {
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è:', error);
    }
  };

  const handleNotificationClick = (notification: Notification) => {
    // –ü–æ–º–µ—á–∞–µ–º –∫–∞–∫ –ø—Ä–æ—á–∏—Ç–∞–Ω–Ω–æ–µ, —Ç–æ–ª—å–∫–æ –µ—Å–ª–∏ –æ–Ω–æ –Ω–µ –±—ã–ª–æ –ø—Ä–æ—á–∏—Ç–∞–Ω–æ
    if (!notification.read) {
      markAsRead(notification._id);
      // –û–ø—Ç–∏–º–∏—Å—Ç–∏—á–Ω–æ–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ
      setNotifications(prev => prev.map(n => n._id === notification._id ? { ...n, read: true } : n));
    }

    // –ï—Å–ª–∏ —ç—Ç–æ –∑–∞—è–≤–∫–∞, –ø–µ—Ä–µ—Ö–æ–¥–∏–º –≤–æ "–í—Ö–æ–¥—è—â–∏–µ"
    if (notification.type === 'request' || notification.type === 'homework_submission') {
      navigateToRequests();
    }
    
    handleMenuClose();
  };
  
  const handleViewAllClick = () => {
    navigateToRequests();
    handleMenuClose();
  }

  return (
    <div>
      <IconButton color="inherit" onClick={handleMenuOpen}>
        <Badge badgeContent={unreadCount} color="error">
          <NotificationsIcon />
        </Badge>
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        open={open}
        onClose={handleMenuClose}
        PaperProps={{
          style: {
            maxHeight: 300,
            width: '400px',
          },
        }}
      >
        <Box sx={{ p: 2, borderBottom: '1px solid #eee' }}>
            <Typography variant="h6">–£–≤–µ–¥–æ–º–ª–µ–Ω–∏—è</Typography>
        </Box>
        {notifications.length === 0 ? (
          <MenuItem disabled>–£–≤–µ–¥–æ–º–ª–µ–Ω–∏–π –Ω–µ—Ç</MenuItem>
        ) : (
          notifications.map((notification) => (
            <MenuItem
              key={notification._id}
              onClick={() => handleNotificationClick(notification)}
              sx={{ 
                bgcolor: notification.read ? 'transparent' : 'action.hover',
                whiteSpace: 'normal',
                alignItems: 'flex-start'
              }}
            >
              <ListItemText
                primary={notification.content}
                secondary={new Date(notification.createdAt).toLocaleString()}
              />
              <ListItemSecondaryAction>
                {/* üëá –ü–µ—Ä–µ–¥–∞–µ–º `event` –≤ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ */}
                <MuiIconButton
                  edge="end"
                  aria-label="delete"
                  onClick={(event) => handleDeleteNotification(event, notification._id)}
                >
                  <DeleteIcon fontSize="small" />
                </MuiIconButton>
              </ListItemSecondaryAction>
            </MenuItem>
          ))
        )}
        <Divider />
        <MenuItem onClick={handleViewAllClick}>
            <ListItemText primary="–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å –≤—Å–µ –≤–æ '–í—Ö–æ–¥—è—â–∏—Ö'" sx={{ textAlign: 'center' }} />
        </MenuItem>
      </Menu>
    </div>
  );
};